*{box-sizing:border-box}body{margin:0;font-family:Montserrat, sans-serif}figure{margin:0}.App{height:100vh;overflow:scroll;-webkit-scroll-snap-type:y mandatory;-ms-scroll-snap-type:y mandatory;scroll-snap-type:y mandatory}.scrolledsection{position:relative;scroll-snap-align:start;height:110vh;background-color:#212121}.scrolledsection:nth-child(2){background-color:#243566}.scrolledsection:nth-child(3n) .scrolledsection__bg-image img{object-position:bottom}.scrolledsection__content{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;height:100vh;color:white;text-align:center;font-size:1.2rem}.scrolledsection__content span{display:block}.scrolledsection__inner{border:2px solid white;padding:1rem;transition:opacity 800ms, transform 800ms;opacity:0;transform:translate3d(0, -1.5rem, 0)}.scrolledsection__inner.is-visible{opacity:1;transform:translate3d(0, 0, 0)}.scrolledsection__bg-image{width:100%;height:100%;position:absolute;top:0;left:0}.scrolledsection__bg-image img{width:100%;height:100%;object-fit:cover;display:block}.scrolledsection__bg-image::after{display:block;content:"";width:100%;height:100%;position:absolute;top:0;left:0;background:rgba(0,0,0,0.3);transition:background 1000ms}.scrolledsection__bg-image.is-visible::after{background:rgba(0,0,0,0.3)}

